@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  15% {
    transform: translateX(-5px);
  }
  30% {
    transform: translateX(5px);
  }
  45% {
    transform: translateX(-5px);
  }
  60% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
  90% {
    transform: translateX(5px);
  }
}

@keyframes pulseGreen {
  0% {
    background-color: var(--mantine-color-teal-6);
    opacity: 1;
  }
  100% {
    background-color: transparent;
  }
}

.shake-and-pulse {
  background-color: green;
  animation:
    shake 0.5s ease,
    pulseGreen 1s ease-in-out;
}
